.register {
    .container_register {
      background-color: var(--pink);
      margin: auto;
      .wrap_content {
        padding: 2rem 0;
        .form_register {
          width: 25rem;
          height: auto;
          background-color: white;
          padding: 5rem 5rem;
          box-sizing: unset;
          border-radius: 10px;
          text-align: center;
          margin: auto;
          display: flex;
          flex-direction: column;
          gap: 1rem;
          .input_group {
            position: relative;
            .icon {
              position: absolute;
              top: 0.8rem;
              left: 0.8rem;
              font-size: 1.4rem;
              color: var(--gray);
            }
            .txt {
              width: 100%;
              border: 1px solid black;
              text-align: center;
              padding: 1rem 3rem;
              border-radius: 5px;
              font-size: 1rem;
            }
            h1 {
              font-weight: 700;
              font-size: 1.5rem;
              padding-bottom: 1.5rem;
              text-align: center;
            }
          }
  
          .forgot {
            float: right;
            color: black;
            margin: 0.5rem 0;
            font-size: 1rem;
          }
  
          .register {
            color: var(--pink);
          }
  
          .line {
            display: block;
            position: relative;
            margin: 0.2rem 0;
  
            &::after {
              content: "";
              position: absolute;
              bottom: 0;
              left: 0;
              top: 55%;
              width: 45%;
              height: 1px;
              background-color: var(--gray);
            }
            &::before {
              content: "";
              position: absolute;
              bottom: 0;
              left: 55%;
              top: 55%;
              width: 45%;
              height: 1px;
              background-color: var(--gray);
            }
          }
  
          .btn_all {
            width: 100%;
            height: 3rem;
            padding: 0.5rem 0;
            margin: 1rem 0;
            font-size: 1rem;
            font-weight: 600;
            border-radius: 5px;
            text-transform: capitalize;
  
            &:hover {
              box-shadow: 0 0 0.1rem var(--pink);
            }
          }
  
          .btn_register {
            background-color: black;
            color: white;
            border: none;
  
            &:hover {
                background-color: var(--pink);
            }
          }
        }
      }
    }
  }
  